@import 'index';

#background {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: -2;
  background: var(--background-color);
  color: var(--text-color);
  transition-duration: .4s;
}

@keyframes noise {
  0% { transform: translate(20%,-15%); }
  10% { transform: translate(-20%,-15%); }
  20% { transform: translate(20%,-5%); }
  30% { transform: translate(-20%,-5%); }
  40% { transform: translate(20%,5%); }
  50% { transform: translate(-20%,5%); }
  60% { transform: translate(20%,15%); }
  70% { transform: translate(-20%,15%); }
  80% { transform: translate(20%,5%); }
  90% { transform: translate(-20%,5%); }
  100% { transform: translate(20%,-5%); }
}

#noise {
  background-repeat: repeat;
  position: fixed;
  z-index: 200;
  width: 300%;
  height: 300%;
  left: -100%;
  top: -100%;
  pointer-events: none;
  animation: noise 0.5s steps(10) infinite;
}

.hooks-main {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
}
.b1,
.b2,
.b3 {
  position: absolute;
  will-change: transform;
  border-radius: 50%;
  background: #d292f0;
  opacity: 0.6;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 1);
}
.b1 {
  width: 170px;
  height: 170px;
}
.b2 {
  width: 350px;
  height: 350px;
}
.b3 {
  width: 200px;
  height: 200px;
}
.b1::after,
.b2::after,
.b3::after {
  content: '';
  position: absolute;
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 1);
}
.b2::after {
  top: 70px;
  left: 70px;
  width: 70px;
  height: 70px;
}
.b3::after {
  top: 50px;
  left: 50px;
  width: 50px;
  height: 50px;
}
.hooks-filter {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: url('#goo');
  overflow: hidden;
}

.Hero {
  height: 100vh;
  width: 100%;
  top: 0;
  left: 0;
  margin: 0;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .name, .bottom {
    width: 100%;
    overflow: hidden;
    font-size: 11vw;
    div {
      width: 100%;
      font-family: 'Dahlia';
      font-weight: 400;
      line-height: 0.55;
      padding-bottom: 2.8vw;
      transition: color .4s;
      animation-delay: 0.2s !important;
    }
  }
  .hidden {
    opacity: 0;
    pointer-events: none;
  }
  .link {
    width: auto;
    position: fixed;
    top: 40px;
    left: 40px;
    z-index: 100;
  }
  .center {
    width: 100%;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      text-align: center;
      font-size: 1.25rem;
      transition-duration: .4s;
      overflow: hidden;
      div {
        animation-delay: 0.2s !important;
      }
    }
    .button {
      animation-delay: 0.2s !important;
    }
  }
  .bottom div {
      text-align: right;
      padding-bottom: 2vw;
  }
  
  .checkbox {
    display: none;
  }
  .label {
    transition-duration: .4s;
    height: 2.4rem;
    width: 8rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    border: 3px solid rgba(var(--text-color-rgb), 0.15);
    border-radius: 100vw;
    position: absolute;
    bottom: 40px;
    left: 40px;
    animation-delay: 0.2s !important;
    p {
      font-size: 1rem;
      text-align: left;
      transition-duration: .4s;
    }
    .ball {
      position: absolute;
      background: var(--text-color);
      width: 3.4rem;
      height: 1.4rem;
      border-radius: 100vw;
      transform: translateX(-1.8rem);
      transition-duration: .4s;
    }
    &:hover {
      cursor: pointer;
      background: var(--text-color);
      p {
        color: var(--background-color);
      }
      .ball {
        background: var(--background-color);
      }
    }
  }
  .checkbox:checked ~ .label {
    .ball {
      transform: translateX(1.8rem);
    }
    .moon {
      opacity: 1;
    }
    .sun {
      opacity: 0;
    }
  }
}

@media screen and (max-width: 1000px) {
  .Hero {
    height: auto;
    padding: 40px;
    align-items: center;
    .name, .bottom {
      font-size: 80px;
      div {
        line-height: 1;
        padding-bottom: 0;
      }
    }
    .link {
      width: 100%;
      text-align: center;
      top: 40px;
      left: 0;
    }
    .center {
      padding: 60px 0;
      justify-content: flex-start;
      align-items: center ;
      p {
        font-size: 1rem;
        text-align: center;
      }
    }
    .bottom div {
      padding-bottom: 60px;
      text-align: center;    }
    .label {
      position: static;
      bottom: unset;
      left: unset;
    }
  }
}

hr {
  width: calc(100% - 80px);
  margin: 0 40px;
  height: 0;
  border-width: 2px 0 0 0;
  border-image-slice: 1;
  border-image-source: linear-gradient(to right, rgba(var(--text-color-rgb), 0), rgba(var(--text-color-rgb), 1), rgba(var(--text-color-rgb), 0));
}

.section {
  width: calc(100% - 80px);
  margin: 0 40px;
  padding: 200px 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  transition-duration: .4s;
  animation-delay: 0.2s !important;
  h2 {
    overflow: hidden;
    font-size: 7vw;
    div {
      font-family: 'Dahlia';
      font-weight: 400;
    }
  }
  .txt {
    max-width: 900px;
    font-size: 1.5rem;
    padding: 40px 0;
    line-height: 1.5;
  }
}

.Work {
  h3 {
    overflow: hidden;
    font-size: 4vw;
    margin-top: 80px;
    div {
      font-family: 'Dahlia';
      font-weight: 400;
    }
  }
  .grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 40px;
    padding-top: 60px;
    a {
      width: calc(50% - 20px);
      img {
        width: 100%;
        border-radius: 20px;
        box-shadow: 0 5px 30px -20px black;
      }
    }
  }
  .art {
    margin: 60px auto 0 auto;
    width: 100%;
    height: 30vw;
    background: #fafafa;
    background-image: url(../img/thumbnails/art.png);
    background-size: 75%;
    background-position: center;
    border-radius: 20px;
    transition-duration: .4s;
    background-repeat: no-repeat;
    box-shadow: 0 5px 30px -20px black;
    div {
      border-radius: 20px;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(var(--background-color-rgb), 0.7);
      backdrop-filter: blur(20px);
      opacity: 0;
      transition-duration: .4s;
      font-size: 4rem;
      font-family: 'Dahlia';
    }
    &:hover {
      background-size: 90%;
      div {
        opacity: 1;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .Work{
    h3 {
      font-size: 8vw;
      margin-top: 60px;
      div {
        font-weight: 400;
      }
    }
    .grid {
      gap: 20px;
      padding-top: 40px;
      a {
        width: 100%;
        img {
          border-radius: 10px;
        }
      }
    }
    .art {
      margin: 40px auto 0 auto;
      height: 400px;
      background-size: 110%;
      border-radius: 10px;
      div {
        border-radius: 10px;
        font-size: 40px;
      }
      &:hover {
        background-size: 130%;
      }
    }
  }
}

.About {
  h3 {
    padding: 100px 0 40px 0;
    overflow: hidden;
    font-size: 5vw;
    div {
      font-family: 'Dahlia';
      font-weight: 400;
    }
  }
  .a {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    width: 100%;
    max-width: 980px;
    margin-top: 40px;
    background: var(--background-color);
    border-radius: 20px;
    img {
      width: 100%;
      border-radius: 20px;
      opacity: 0.6;
      mix-blend-mode: luminosity;
      filter: brightness(1.1) contrast(0.8);
    }
    .b {
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 40px;
      p {
        text-shadow: 0px 3px 30px var(--background-color);
      }
    }
  }
  ul {
    columns: 4;
    li {
      width: 240px;
      line-height: 1.5;
      list-style-position: inside;
    }
  }
  hr {
    border-image-source: linear-gradient(to right, rgba(var(--text-color-rgb), 0), rgba(var(--text-color-rgb), 0.3), rgba(var(--text-color-rgb), 0));
    margin: 20px 0;
  }
}

@media screen and (max-width: 1040px) {
  .About ul {
    columns: 3;
  }
}
@media screen and (max-width: 800px) {
  .About ul {
    columns: 2;
  }
}
@media screen and (max-width: 520px) {
  .About ul {
    columns: 1;
  }
}

#Footer {
  width: calc(100% - 80px);
  margin: 0 40px;
  padding: 40px 0;

  .main {
    height: 300px;
    display: flex;
    flex-direction: row;
    font-size: 1.5rem;
    gap: 40px;
    p {
      overflow: hidden;
      div {
        animation-delay: 0.2s !important;
      }
    }
    .a {
      width: 30%;
    }
    .b {
      width: 40%;
    }
  }

  .bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    p {
      overflow: hidden;
      div {
        animation-delay: 0.2s !important;
      }
    }
    .links {
      overflow: hidden;
      div {
        display: flex;
        flex-direction: row;
        gap: 40px;
        animation-delay: 0.2s !important;
      }
    }
  }
}
@media screen and (max-width: 900px) {
  #Footer {
    width: calc(100% - 40px);
    margin: 0 20px;
    padding: 20px 0;
    .main {
      flex-direction: column;
      margin-bottom: 80px;
      height: auto;
      font-size: 1.25rem;
      .a, .b {
        width: 100%;
      }
    }
  }
}
@media screen and (max-width: 700px) {
  hr {
    width: calc(100% - 40px);
    margin: 0 20px;
  }
  .section {
    width: calc(100% - 40px);
    margin: 0 20px;
    padding: 120px 0;
    h2 {
      font-size: 16vw;
    }
  }
  .About {
    .a {
      background: none;
      img {
        opacity: 1;
      }
      .b {
        position: static;
        width: 100%;
        padding: 0;
        margin-top: 30px;
        .txt {
          font-size: 1rem;
          padding: 0 20px 0 0;
        }
      }
    }
    h3 {
      font-size: 8vw;
    }
  }
  #Footer .bottom {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 20px;
    .links {
      text-decoration: underline;
    }
  }
}