@font-face {
  font-family: 'Radio Grotesk';
  src: url('../fonts/RadioGrotesk-Regular.eot'), url('../fonts/RadioGrotesk-Regular.woff') format('woff'), url('../fonts/RadioGrotesk-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Dahlia';
  src: url('../fonts/dahlia-regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  font-family: neue-haas-grotesk-display, sans-serif;
  font-weight: 500;
  box-sizing: border-box;
  color: var(--text-color);
}

html {
  font-size: 18px;
  scroll-behavior: smooth;
}

@media (prefers-color-scheme: dark) {
  html {
    background: black;
  }
}
@media (prefers-color-scheme: light) {
  html {
    background: white;
  }
}

body {
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: var(--text-color);
}

.button {
  text-align: center;
  display: block;
  padding: 12px 36px;
  border: 3px solid rgba(var(--text-color-rgb), 0.15);
  margin: 1.15rem 0 0 0;
  border-radius: 100vw;
  transition-duration: .4s;
  .textouter {
    display: block;
    padding: 4px 2px;
    position: relative;
    overflow: hidden;
    .text {
      position: relative;
      display: block;
      transform-origin: left top;
      color: var(--text-color);
      transition-duration: .4s;
      &::after {
        content: attr(data-text);
        display: block;
        position: absolute;
        top: 100%;
        color: var(--background-color);
        transform-origin: left top;
        transform: translateY(100%) skewY(-15deg);
        transition-duration: .4s;
      }
    }
  }
  &:hover {
    background: var(--text-color);  
    .text {
      transform: translateY(-100%) skewY(-15deg);
      &::after {
        transform: translateY(0%) skewY(15deg);
      }
    } 
  }
}
